@import '../../scss/variable.scss';
$trs: all ease 0.3s 0s;

.container {
  padding: 0;
  color: black;
  height: fit-content;
  a {
    h3 {
      font-size: $ft-s-title;
    }
    h3:hover {
      font-weight: bold;
    }
  }
  .wrapper {
    border-radius: 0px;
    width: 100%;
    text-align: center;
    min-height: 150px;
    display: flex;
    justify-content: center;
    background-color: var(--bkg-color-2);
    border: none;

    .icon {
      &--pointer {
        background-image: url(../../assets/image/pointer.svg);
        width: 50px;
        height: 50px;
        position: absolute;
        z-index: 1;
        animation: bounce 2s infinite;
        @keyframes bounce {
          0%,
          20%,
          50%,
          80%,
          100% {
            transform: translateY(0);
          }
          40% {
            transform: translateY(-20px);
          }
          60% {
            transform: translateY(-10px);
          }
        }
      }
    }

    // Iframe Video
    .iframe {
      &--wrapper {
        position: relative;
        height: 0;
        padding-bottom: 56.25%;
      }
      &--video {
        max-width: 100%;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
      }
    }

    // Image
    .image {
      max-width: 100%;
    }

    // Placeholder
    .placeholder {
      position: relative;
      height: 0;
      padding-bottom: 56.25%;
      display: flex;
      justify-content: center;
      background-color: var(--bkg-color);
      span {
        position: absolute;
        top: 45%;
        font-size: $ft-s;
      }
    }
  }
}

.mobile-btn {
  font-size: 2vh;
  border-bottom: 2px solid;
  width: fit-content;
  line-height: 1;
}

//== DEVICE SCREEN ==//
/*대부분의 스마트폰 모바일 기기(세로)*/
@media (min-width: 320px) and (max-width: 480px) {
  .container {
    .wrapper {
      border-left: none;
      border-right: none;
    }
  }
}

/*저해상도 태블릿, 모바일(가로)*/
@media (min-width: 481px) and (max-width: 767px) {
  .container {
    border-left: none !important;
    border-right: none !important;
  }
}
